 /* appear - on page load */
.fade-appear {
    opacity: 0;
    z-index: 1;
}
.fade-appear.fade-appear-active {
  opacity: 1;
  transition: opacity 1000ms linear;
}

/* enter */
.fade-enter {
  opacity: 0;
  z-index: 1;
}
.fade-enter.fade-enter-active {
    opacity: 1;
    transition: opacity 5000ms linear 5000ms;
}

/* exit */
.fade-exit {
    opacity: 1;
}
.fade-exit.fade-exit-active {
    opacity: 0;
    transition: opacity 5000ms linear;
}
.fade-exit-done {
  opacity: 0;
}

/* Languages tab transition*/
.l-node-enter {
  visibility: visible;
  opacity: 1;
  transition: visibility 0s linear 0s, opacity 300ms;
}
.l-node-enter-active {
  opacity: 1;
  transition: opacity 200ms;
}
.l-node-exit {
  visibility: hidden;
    opacity: 0;
    transition: visibility 0s linear 300ms, opacity 300ms;
}
.l-node-exit-active {
  opacity: 0;
  transition: opacity 200ms;
}
/* Web/Client tab transition*/
.wc-node-enter {
  opacity: 0;
}
.wc-node-enter-active {
  opacity: 1;
  transition: opacity 200ms;
}
.wc-node-exit {
  opacity: 1;
}
.wc-node-exit-active {
  opacity: 0;
  transition: opacity 200ms;
}
/* Database tab transition*/
.db-node-enter {
